import {
    Avatar,
    Grid, List,
    ListItem,
    ListItemAvatar,
    ListItemText,
    Paper, Tab, Tabs,
} from "@mui/material";
import Web3 from "web3";
import coineus_abi from '../abis/coineus.json';
import {useSelector} from "react-redux";
import {useEffect, useState} from "react";
import {makeBatchRequest} from "../utils/promisify";
import {CoineusCryptoFormat, CoineusUSDFormat} from "../utils/currency_format";
import {formatWeiToNumber} from "../utils/format";
import consensus_abi from "../abis/consensus_abi.json";
import {makeStyles} from "@mui/styles";
import clsx from "clsx";
import bbks_abi from "../abis/bbk_abi.json";
import srgone_abi from "../abis/Coineus/SRGONE.json";

const coineus_fund_address = "0xb80267eA7fa368374ee4d4Bf10044778232AdEFe";
const assurance_fund_address = "0x09501F8Ca9476d0c516E121FB4dCE4B7b2301d51";
const coineus_validator_address = "0x53e2033F86C1a54bEbAE893DC8C9fAe179b3Faa2";
const consensusAddress = "0x3014ca10b91cb3D0AD85fEf7A3Cb95BCAc9c0f79";

const useStyles = makeStyles((theme) => ({
    networkChip: {
        borderRadius: "50%",
        marginLeft: 30,
        position: 'absolute',
        top: 42,
        height: 16,
        width: 16,
        display: 'inline-block',
        backgroundSize: 'cover',
        '&.fuse': {
            backgroundImage: `url('https://coineus.app/assets/tokens/fuse/logo.png')`,
        },
        '&.avax': {
            backgroundImage: `url('https://coineus.app/assets/tokens/avax/logo.png')`,
        },
        '&.bnb': {
            backgroundImage: `url('https://coineus.app/assets/tokens/bnb/logo.png')`,
        },
        '&.eth': {
            backgroundImage: `url('https://coineus.app/assets/tokens/eth/logo.png')`,
        },
        '&.arb': {
            backgroundImage: `url('https://coineus.app/assets/tokens/arb/chain.png')`,
        }
    }
}));

export default function CoineusPortfolio() {

    const {wallet, rpc} = useSelector(state => state.coineus);

    const [data, setData] = useState({});
    const [chainTab, setChainTab] = useState('fuse');

    const [fuseData, setFuseData] = useState({});
    const [bnbData, setBnbData] = useState({});
    const [ethData, setEthData] = useState({});
    const [arbData, setArbData] = useState({});

    useEffect(() => {

        const web3FUSE = new Web3(rpc.fuse);
        const consensusContract = new web3FUSE.eth.Contract(consensus_abi, consensusAddress);
        const ceus_contract = new web3FUSE.eth.Contract(coineus_abi, "0x4e69Ae0CD024754655b4eF74F24A8DCB39Ba07e8");
        const ceusone_contract = new web3FUSE.eth.Contract(coineus_abi, "0xE27431FACb1F093BA164c9F86B3f484b39ad54CC");
        const lit_contract = new web3FUSE.eth.Contract(coineus_abi, '0xF2C6C1AA2bf8ec40F564Ea8A483F64907ea37A3F');


        const callsF = [
            web3FUSE.eth.getBalance(coineus_fund_address),
            web3FUSE.eth.getBalance(assurance_fund_address),
            consensusContract.methods.delegatedAmount(coineus_fund_address, coineus_validator_address).call,
            consensusContract.methods.delegatedAmount(assurance_fund_address, coineus_validator_address).call,
            ceus_contract.methods.balanceOf(coineus_fund_address).call,
            ceusone_contract.methods.balanceOf(coineus_fund_address).call,
            lit_contract.methods.balanceOf(coineus_fund_address).call,
        ];

        makeBatchRequest(web3FUSE, callsF).then(resp => {
            let [
                coineus_fund_balance,
                assurance_fund_balance,
                coineus_fund_staked,
                assurance_fund_staked,
                ceus_fund_balance,
                ceusone_fund_balance,
                lit_fund_balance
            ] = resp;

            coineus_fund_balance = coineus_fund_balance / (10 ** 18);
            assurance_fund_balance = assurance_fund_balance / (10 ** 18);
            coineus_fund_staked = coineus_fund_staked / (10 ** 18);
            assurance_fund_staked = assurance_fund_staked / (10 ** 18);
            ceus_fund_balance = ceus_fund_balance / (10 ** 18);
            ceusone_fund_balance = ceusone_fund_balance / (10 ** 18);
            lit_fund_balance = lit_fund_balance / (10 ** 18);

            setFuseData(
                {
                    coineus_fund_balance,
                    assurance_fund_balance,
                    coineus_fund_staked,
                    assurance_fund_staked,
                    ceus_fund_balance,
                    ceusone_fund_balance,
                    lit_fund_balance
                }
            )
        })

        const web3AVAX = new Web3(rpc.avax);
        const dgnx_contract = new web3AVAX.eth.Contract(coineus_abi, '0x51e48670098173025C477D9AA3f0efF7BF9f7812');

        const calls = [
            web3AVAX.eth.getBalance(coineus_fund_address),
            dgnx_contract.methods.balanceOf(coineus_fund_address).call,
        ];

        makeBatchRequest(web3AVAX, calls).then(resp => {
            let [
                coineus_avax_balance,
                coineus_fund_dgnx_balance,
            ] = resp;

            setData({
                coineus_avax_balance: coineus_avax_balance / (10 ** 18),
                coineus_fund_dgnx_balance
            })

        });

        const web3BNB = new Web3(rpc.bnb);
        const lit_bnb_contract = new web3BNB.eth.Contract(bbks_abi, '0xbC8F89861737C3B8c60a05165E0a5C5B8A137fAc');
        const srg_bnb_contract = new web3BNB.eth.Contract(coineus_abi, '0x9f19c8e321bD14345b797d43E01f0eED030F5Bff');
        const srgone_bnb_contract = new web3BNB.eth.Contract(srgone_abi, '0x14F91F92aF274DDFF7f1A7f187B5eB5385F5f772');

        const callsBNB = [
            web3BNB.eth.getBalance(coineus_fund_address),
            lit_bnb_contract.methods.balanceOf(coineus_fund_address).call,
            srg_bnb_contract.methods.balanceOf(coineus_fund_address).call,
            srgone_bnb_contract.methods.balanceOf(coineus_fund_address).call,
        ];

        makeBatchRequest(web3BNB, callsBNB).then(resp => {
            let [
                coineus_bnb_balance,
                lit_bnb_balance,
                srg_bnb_balance,
                srgone_bnb_balance,
            ] = resp;

            setBnbData({
                coineus_bnb_balance: coineus_bnb_balance / (10 ** 18),
                lit_bnb_balance: lit_bnb_balance / (10 ** 9),
                srg_bnb_balance: srg_bnb_balance / (10 ** 9),
                srgone_bnb_balance: srgone_bnb_balance / (10 ** 9)
            })

        });

        const web3ETH = new Web3(rpc.eth);
        const bbks_contract = new web3ETH.eth.Contract(coineus_abi, '0xF654d4C3CC334324ad474A0d5d3708dCA4c1CB25');
        const srg_eth_contract = new web3ETH.eth.Contract(coineus_abi, '0xcD682EF09d07668d49A8103ddD65Ff54AebFbfDe');

        const callsETH = [
            web3ETH.eth.getBalance(coineus_fund_address),
            srg_eth_contract.methods.balanceOf(coineus_fund_address).call,
            bbks_contract.methods.balanceOf(coineus_fund_address).call,
        ];

        makeBatchRequest(web3ETH, callsETH).then(resp => {
            let [
                coineus_eth_balance,
                srg_eth_balance,
                bbks_eth_balance,
            ] = resp;

            setEthData({
                coineus_eth_balance: coineus_eth_balance / (10 ** 18),
                srg_eth_balance: srg_eth_balance / (10 ** 9),
                bbks_eth_balance: bbks_eth_balance / (10 ** 9)
            })

        });

        const web3ARB = new Web3(rpc.arb);
        const srg_arb_contract = new web3ARB.eth.Contract(coineus_abi, '0x31aD8255CB8E428E8B0f6Ab6a6f44804642720aF');
        const srgone_arb_contract = new web3ARB.eth.Contract(srgone_abi, '0xB0eF2f510e3d2CC7c0F545A7c82F3B76eC36B239');
        const pepe_contract = new web3ARB.eth.Contract(coineus_abi, '0x722D8fA60Fe59a14aBd194D94F1090Aa473A8577');

        const callsARB = [
            web3ARB.eth.getBalance(coineus_fund_address),
            srg_arb_contract.methods.balanceOf(coineus_fund_address).call,
            srgone_arb_contract.methods.balanceOf(coineus_fund_address).call,
            pepe_contract.methods.balanceOf(coineus_fund_address).call
        ];

        makeBatchRequest(web3ARB, callsARB).then(resp => {

            let [
                coineus_arb_balance,
                srg_arb_balance,
                srgone_arb_balance,
                pepe_arb_balance,
            ] = resp;

            setArbData({
                coineus_arb_balance: coineus_arb_balance / (10 ** 18),
                srg_arb_balance: srg_arb_balance / (10 ** 9),
                srgone_arb_balance: srgone_arb_balance / (10 ** 9),
                pepe_arb_balance: pepe_arb_balance / (10 ** 9)
            })

        });

    }, []);

    const totalValue =
        (
            (
                fuseData.coineus_fund_balance +
                fuseData.coineus_fund_staked +
                fuseData.assurance_fund_balance +
                fuseData.assurance_fund_staked
            ) * wallet.prices.fuse?.native?.inUSD
        ) + //FUSE
        ((fuseData.ceus_fund_balance) * wallet.prices.fuse?.["0x4e69Ae0CD024754655b4eF74F24A8DCB39Ba07e8"]?.inUSD) + //CEUS
        ((fuseData.ceusone_fund_balance) * wallet.prices.fuse?.["0x4e69Ae0CD024754655b4eF74F24A8DCB39Ba07e8"]?.inUSD) + //CEUSONE
        ((fuseData.lit_fund_balance) * wallet.prices.fuse?.["0xF2C6C1AA2bf8ec40F564Ea8A483F64907ea37A3F"]?.inUSD) + //LIT

        (data.coineus_avax_balance * wallet.prices.avax.native?.inUSD) + //AVAX
        formatWeiToNumber(data.coineus_fund_dgnx_balance) * wallet.prices.avax?.['0x51e48670098173025C477D9AA3f0efF7BF9f7812']?.inUSD +//DGNX

        bnbData.coineus_bnb_balance * wallet.prices.bnb.native?.inUSD +// BNB
        ((bnbData.lit_bnb_balance) * wallet.prices.bnb?.["0xbC8F89861737C3B8c60a05165E0a5C5B8A137fAc"]?.inUSD) + //LIT
        ((bnbData.srg_bnb_balance) * wallet.prices.bnb?.["0x9f19c8e321bD14345b797d43E01f0eED030F5Bff"]?.inUSD) + //SRG
        ((bnbData.srgone_bnb_balance) * wallet.prices.bnb?.["0x9f19c8e321bD14345b797d43E01f0eED030F5Bff"]?.inUSD) + //SRGONE

        ethData.coineus_eth_balance * wallet.prices.eth.native?.inUSD +// eth
        (ethData.bbks_eth_balance * wallet.prices.eth?.['0xF654d4C3CC334324ad474A0d5d3708dCA4c1CB25']?.inUSD) + //BBKS
        (ethData.srg_eth_balance * wallet.prices.eth?.["0xcD682EF09d07668d49A8103ddD65Ff54AebFbfDe"]?.inUSD) +//SRG

        arbData.coineus_arb_balance * wallet.prices.arb.native?.inUSD +// eth
        (arbData.pepe_arb_balance * wallet.prices.arb?.['0x722D8fA60Fe59a14aBd194D94F1090Aa473A8577']?.inUSD) + //BBKS
        (arbData.srg_arb_balance * wallet.prices.arb?.["0x31aD8255CB8E428E8B0f6Ab6a6f44804642720aF"]?.inUSD) +//SRG
        (arbData.srgone_arb_balance * wallet.prices.arb?.["0x31aD8255CB8E428E8B0f6Ab6a6f44804642720aF"]?.inUSD) //SRGONE

    return <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
        <Grid item xs={12}>
            <Paper style={{color: '#000', backgroundColor: '#fbca00'}}>
                <ListItem>
                    <ListItemText
                        primary={CoineusUSDFormat(totalValue)}
                        secondary="Total Fund Value"
                        primaryTypographyProps={{fontWeight: 700, fontSize: '2.125rem', textAlign: 'right'}}
                        secondaryTypographyProps={{color: '#000', textAlign: 'right'}}
                    />
                </ListItem>
            </Paper>
        </Grid>

        <Grid item xs={12}>
            <Tabs
                value={chainTab}
                onChange={(ev, val) => {
                    setChainTab(val)
                }}
                variant="scrollable"
                scrollButtons="auto"
            >
                <Tab id="fuse" value="fuse" label="FUSE"/>
                <Tab id="avax" value="avax" label="AVAX"/>
                <Tab id="bnb" value="bnb" label="BNB"/>
                <Tab id="eth" value="eth" label="ETH"/>
                <Tab id="arb" value="arb" label="ARB"/>
            </Tabs>
        </Grid>

        {
            chainTab === 'fuse' && <>

                <TokenStatsSection data={{
                    symbol: "FUSE",
                    label: "Fund Holdings",
                    logo: "https://coineus.app/assets/tokens/fuse/logo.png",
                    usdValue: CoineusUSDFormat((
                        fuseData.coineus_fund_balance +
                        fuseData.coineus_fund_staked +
                        fuseData.assurance_fund_balance +
                        fuseData.assurance_fund_staked
                    ) * wallet.prices.fuse?.native?.inUSD),
                    balance: CoineusCryptoFormat(fuseData.coineus_fund_balance + fuseData.assurance_fund_balance),
                    secondaryBalance: CoineusCryptoFormat(fuseData.coineus_fund_staked + fuseData.assurance_fund_staked),
                    secondarySymbol: 'FUSE',
                    secondaryLabel: 'Staked',
                    network: 'fuse',
                    native: true
                }}/>

                <TokenStatsSection data={{
                    symbol: "CEUS",
                    label: "Fund Holdings",
                    logo: "https://coineus.app/assets/tokens/fuse/0x4e69Ae0CD024754655b4eF74F24A8DCB39Ba07e8/logo.png",
                    usdValue: CoineusUSDFormat((
                        fuseData.ceus_fund_balance
                    ) * wallet.prices.fuse?.["0x4e69Ae0CD024754655b4eF74F24A8DCB39Ba07e8"]?.inUSD),
                    balance: CoineusCryptoFormat(fuseData.ceus_fund_balance),
                    network: 'fuse'
                }}/>

                <TokenStatsSection data={{
                    symbol: "CEUSONE",
                    label: "Fund Holdings",
                    logo: "https://coineus.app/assets/tokens/fuse/0xE27431FACb1F093BA164c9F86B3f484b39ad54CC/logo.png",
                    usdValue: CoineusUSDFormat((
                        fuseData.ceusone_fund_balance
                    ) * wallet.prices.fuse?.["0x4e69Ae0CD024754655b4eF74F24A8DCB39Ba07e8"]?.inUSD),
                    balance: CoineusCryptoFormat(fuseData.ceusone_fund_balance),
                    network: 'fuse'
                }}/>

                <TokenStatsSection data={{
                    symbol: "LIT",
                    label: "Fund Holdings",
                    logo: "https://coineus.app/assets/tokens/fuse/0xF2C6C1AA2bf8ec40F564Ea8A483F64907ea37A3F/logo.png",
                    usdValue: CoineusUSDFormat((
                        fuseData.lit_fund_balance
                    ) * wallet.prices.fuse?.["0xF2C6C1AA2bf8ec40F564Ea8A483F64907ea37A3F"]?.inUSD),
                    balance: CoineusCryptoFormat(fuseData.lit_fund_balance),
                    network: 'fuse'
                }}/>

            </>
        }

        {
            chainTab === 'bnb' && <>

                <TokenStatsSection data={{
                    symbol: "BNB",
                    label: "Fund Holdings",
                    logo: "https://coineus.app/assets/tokens/bnb/logo.png",
                    usdValue: CoineusUSDFormat(bnbData.coineus_bnb_balance * wallet.prices.bnb.native?.inUSD),
                    balance: CoineusCryptoFormat(bnbData.coineus_bnb_balance),
                    network: 'bnb'
                }}/>

                <TokenStatsSection data={{
                    symbol: "LIT",
                    label: "Fund Holdings",
                    logo: "https://coineus.app/assets/tokens/bnb/0xbC8F89861737C3B8c60a05165E0a5C5B8A137fAc/logo.png",
                    usdValue: CoineusUSDFormat(bnbData.lit_bnb_balance * wallet.prices.bnb?.["0xbC8F89861737C3B8c60a05165E0a5C5B8A137fAc"]?.inUSD),
                    balance: CoineusCryptoFormat(bnbData.lit_bnb_balance),
                    network: 'bnb'
                }}/>

                <TokenStatsSection data={{
                    symbol: "SRG",
                    label: "Fund Holdings",
                    logo: "https://coineus.app/assets/tokens/bnb/0x9f19c8e321bD14345b797d43E01f0eED030F5Bff/logo.png",
                    usdValue: CoineusUSDFormat(bnbData.srg_bnb_balance * wallet.prices.bnb?.["0x9f19c8e321bD14345b797d43E01f0eED030F5Bff"]?.inUSD),
                    balance: CoineusCryptoFormat(bnbData.srg_bnb_balance),
                    network: 'bnb'
                }}/>

                <TokenStatsSection data={{
                    symbol: "SRGONE",
                    label: "Fund Holdings",
                    logo: "https://coineus.app/assets/tokens/bnb/0x14F91F92aF274DDFF7f1A7f187B5eB5385F5f772/logo.png",
                    usdValue: CoineusUSDFormat(bnbData.srgone_bnb_balance * wallet.prices.bnb?.["0x14F91F92aF274DDFF7f1A7f187B5eB5385F5f772"]?.inUSD),
                    balance: CoineusCryptoFormat(bnbData.srgone_bnb_balance),
                    network: 'bnb'
                }}/>

            </>
        }

        {
            chainTab === 'avax' && <>
                <TokenStatsSection data={{
                    symbol: "AVAX",
                    label: "Fund Holdings",
                    logo: "https://coineus.app/assets/tokens/avax/logo.png",
                    usdValue: CoineusUSDFormat(data.coineus_avax_balance * wallet.prices.avax.native?.inUSD),
                    balance: CoineusCryptoFormat(data.coineus_avax_balance),
                    network: 'avax',
                    native: true
                }}/>

                <TokenStatsSection data={{
                    symbol: "DGNX",
                    label: "Fund Holdings",
                    logo: "https://coineus.app/assets/tokens/avax/0x51e48670098173025C477D9AA3f0efF7BF9f7812/logo.png",
                    usdValue: CoineusUSDFormat((data.coineus_fund_dgnx_balance / 10 ** 18) * wallet.prices.avax?.['0x51e48670098173025C477D9AA3f0efF7BF9f7812']?.inUSD),
                    balance: CoineusCryptoFormat((data.coineus_fund_dgnx_balance / 10 ** 18)),
                    network: 'avax'
                }}/>

            </>
        }

        {
            chainTab === 'eth' && <>

                <TokenStatsSection data={{
                    symbol: "ETH",
                    label: "Fund Holdings",
                    logo: "https://coineus.app/assets/tokens/eth/logo.png",
                    usdValue: CoineusUSDFormat(ethData.coineus_eth_balance * wallet.prices.eth.native?.inUSD),
                    balance: CoineusCryptoFormat(ethData.coineus_eth_balance),
                    network: 'eth',
                    native: true
                }}/>

                <TokenStatsSection data={{
                    symbol: "BBKS",
                    label: "Fund Holdings",
                    logo: "https://coineus.app/assets/tokens/eth/0xF654d4C3CC334324ad474A0d5d3708dCA4c1CB25/logo.png",
                    usdValue: CoineusUSDFormat(ethData.bbks_eth_balance * wallet.prices.eth?.['0xF654d4C3CC334324ad474A0d5d3708dCA4c1CB25']?.inUSD),
                    balance: CoineusCryptoFormat(ethData.bbks_eth_balance),
                    network: 'eth'
                }}/>

                <TokenStatsSection data={{
                    symbol: "SRG",
                    label: "Fund Holdings",
                    logo: "https://coineus.app/assets/tokens/eth/0xcD682EF09d07668d49A8103ddD65Ff54AebFbfDe/logo.png",
                    usdValue: CoineusUSDFormat(ethData.srg_eth_balance * wallet.prices.eth?.["0xcD682EF09d07668d49A8103ddD65Ff54AebFbfDe"]?.inUSD),
                    balance: CoineusCryptoFormat(ethData.srg_eth_balance),
                    network: 'eth'
                }}/>
            </>
        }

        {
            chainTab === 'arb' && <>

                <TokenStatsSection data={{
                    symbol: "ETH",
                    label: "Fund Holdings",
                    logo: "https://coineus.app/assets/tokens/eth/logo.png",
                    usdValue: CoineusUSDFormat(arbData.coineus_arb_balance * wallet.prices.arb?.native?.inUSD),
                    balance: CoineusCryptoFormat(arbData.coineus_arb_balance),
                    network: 'arb',
                    native: true
                }}/>

                <TokenStatsSection data={{
                    symbol: "$PEPE",
                    label: "Fund Holdings",
                    logo: "https://coineus.app/assets/tokens/arb/0x722D8fA60Fe59a14aBd194D94F1090Aa473A8577/logo.png",
                    usdValue: CoineusUSDFormat(arbData.pepe_arb_balance * wallet.prices.arb?.["0x722D8fA60Fe59a14aBd194D94F1090Aa473A8577"]?.inUSD),
                    balance: CoineusCryptoFormat(arbData.pepe_arb_balance),
                    network: 'arb'
                }}/>

                <TokenStatsSection data={{
                    symbol: "SRG",
                    label: "Fund Holdings",
                    logo: "https://coineus.app/assets/tokens/arb/0x31aD8255CB8E428E8B0f6Ab6a6f44804642720aF/logo.png",
                    usdValue: CoineusUSDFormat(arbData.srg_arb_balance * wallet.prices.arb?.["0x31aD8255CB8E428E8B0f6Ab6a6f44804642720aF"]?.inUSD),
                    balance: CoineusCryptoFormat(arbData.srg_arb_balance),
                    network: 'arb'
                }}/>

                <TokenStatsSection data={{
                    symbol: "SRGONE",
                    label: "Fund Holdings",
                    logo: "https://coineus.app/assets/tokens/arb/0xB0eF2f510e3d2CC7c0F545A7c82F3B76eC36B239/logo.png",
                    usdValue: CoineusUSDFormat(arbData.srgone_arb_balance * wallet.prices.arb?.["0xB0eF2f510e3d2CC7c0F545A7c82F3B76eC36B239"]?.inUSD),
                    balance: CoineusCryptoFormat(arbData.srgone_arb_balance),
                    network: 'arb'
                }}/>
            </>
        }

    </Grid>
}

function TokenStatsSection(props) {

    const classes = useStyles();

    const {
        native,
        symbol,
        label,
        logo,
        usdValue,
        balance,
        secondaryBalance,
        secondarySymbol,
        secondaryLabel,
        network
    } = props.data;

    return <Grid item xs={12}>
        <Paper>
            <List>
                <ListItem>
                    <ListItemAvatar>
                        <Avatar src={logo}/>
                        {!native && <span className={clsx(classes.networkChip, network)}/>}
                    </ListItemAvatar>
                    <ListItemText
                        primary={symbol}
                        primaryTypographyProps={{fontWeight: 700}}
                        secondary={label}
                    />
                    <ListItemText
                        primary={usdValue}
                        secondary="USD Value"
                        primaryTypographyProps={{fontWeight: 700, textAlign: 'right'}}
                        secondaryTypographyProps={{textAlign: 'right'}}
                    />
                </ListItem>
                {
                    balance !== undefined && <ListItem>
                        <ListItemText
                            primary={`${balance} ${symbol}`}
                            primaryTypographyProps={{fontWeight: 700}}
                            secondary="Wallet"
                        />
                        {
                            secondaryBalance !== undefined && <ListItemText
                                primary={`${secondaryBalance} ${secondarySymbol}`}
                                primaryTypographyProps={{fontWeight: 700}}
                                secondary={secondaryLabel}
                            />
                        }
                    </ListItem>
                }
            </List>
        </Paper>
    </Grid>
}