import {
    Button,
    FormControl,
    Grid, List, ListItem, ListItemText,
    Paper,
    TextField,
    Typography
} from "@mui/material";
import Web3 from "web3";
import ceus_abi from "../abis/ceus_abi.json";
import lit_abi from "../abis/Lightning.json";
import litsrg20_abi from "../abis/LITSRG20.json";
import chad_abi from "../abis/CHAD.json";


import {NetworkButton} from "../components/Coineus";
import {CHAIN_IDS} from "../constants";


import {useSelector} from "react-redux";
import {useEffect, useState} from "react";
import CoineusLoading from "../components/CoineusLoading";
import {makeBatchRequest} from "../utils/promisify";
import {formatWeiToNumber} from "../utils/format";
import {CoineusCryptoFormat} from "../utils/currency_format";

const COINEUS_FUND_ADDRESS = "0xb80267eA7fa368374ee4d4Bf10044778232AdEFe";
const CEUS_CONTRACT_ADDRESS = "0x4e69Ae0CD024754655b4eF74F24A8DCB39Ba07e8";
const LIT_CONTRACT_ADDRESS = "0xF2C6C1AA2bf8ec40F564Ea8A483F64907ea37A3F";
const CHAD_CONTRACT_ADDRESS = "0xcC8A1364Bd05e05402e8b28dAe554b5d6216E6B9";
const CHADARB_CONTRACT_ADDRESS = "0x6cD42aAE3249EF5951f0C91b00E953fe0D1202a7";
const LITBNB_CONTRACT_ADDRESS = "0xbC8F89861737C3B8c60a05165E0a5C5B8A137fAc";
const SRGBNB_CONTRACT_ADDRESS = "0x9f19c8e321bD14345b797d43E01f0eED030F5Bff";
const SRGARB_CONTRACT_ADDRESS = "0x31aD8255CB8E428E8B0f6Ab6a6f44804642720aF";

const CHAD_BURNER = "0xd45902275AcA4e99e12Fa4A92B2A110433C16B97";
const CHAD_ASSET_BACKING = "0x90D42Cc7FE837Eba62dbCA7Eca62035529b5f39E";
const CHAD_STAKING = "0x425fA8237b8dA473126CD60Cd73fb0D263231Dfb";

const CHADARB_BURNER = "0x6F9581a7223425ea2ABF0b264dA40FcA37C2Eb1E";
const CHADARB_ASSET_BACKING = "0x72cD16D6a9bBC1576a5B781229040Ee1FC9e56E7";
const CHADARB_STAKING = "0x29329F99cFCa3FFda0C03657343566b58FD4B653";

export default function Admin() {

    const {wallet, rpc} = useSelector(state => state.coineus);

    const [pending, setPending] = useState(false);
    const [amount, setAmount] = useState("");

    const [chadTaxBal, setChadTaxBal] = useState(0);
    const [chadARBTaxBal, setChadARBTaxBal] = useState(0);
    const [litBNBTaxBal, setLitBNBTaxBal] = useState(0);

    const [pendingCHADBurn, setPendingCHADBurn] = useState(0);
    const [pendingCHADARBBurn, setPendingCHADARBBurn] = useState(0);

    const getInfo = () => {
        const web3BSC = new Web3(rpc.bnb);
        const srgbnb_contract = new web3BSC.eth.Contract(chad_abi, SRGBNB_CONTRACT_ADDRESS);
        const chad_contract = new web3BSC.eth.Contract(chad_abi, CHAD_CONTRACT_ADDRESS);
        const litBNB_contract = new web3BSC.eth.Contract(litsrg20_abi, LITBNB_CONTRACT_ADDRESS);

        const callsBSC = [
            chad_contract.methods.taxBalance().call,
            litBNB_contract.methods.taxBalance().call,
            srgbnb_contract.methods.balanceOf(CHAD_BURNER).call
        ];

        makeBatchRequest(web3BSC, callsBSC).then(resp => {
            const [
                chadTaxBal,
                litBNBTaxBal,
                pendingCHADBurn
            ] = resp;

            setChadTaxBal(chadTaxBal);
            setLitBNBTaxBal(litBNBTaxBal);
            setPendingCHADBurn(pendingCHADBurn);
        });

        const web3ARB = new Web3(rpc.arb);
        const srgarb_contract = new web3ARB.eth.Contract(chad_abi, SRGARB_CONTRACT_ADDRESS);
        const chadarb_contract = new web3ARB.eth.Contract(chad_abi, CHADARB_CONTRACT_ADDRESS);

        const callsARB = [
            chadarb_contract.methods.taxBalance().call,
            srgarb_contract.methods.balanceOf(CHADARB_BURNER).call
        ];

        makeBatchRequest(web3ARB, callsARB).then(resp => {
            const [
                chadARBTaxBal,
                pendingCHADARBBurn
            ] = resp;

            setChadARBTaxBal(chadARBTaxBal);
            setPendingCHADARBBurn(pendingCHADARBBurn);
        });

    }

    useEffect(getInfo, []);

    const burn = () => {
        const web3 = new Web3(window.provider);
        const ceus_contract = new web3.eth.Contract(ceus_abi, CEUS_CONTRACT_ADDRESS);

        ceus_contract.methods.performBurn(web3.utils.toWei(amount))
            .send({from: wallet.address})
            .once('transactionHash', function (tx) {
                setPending(true)
            })
            .on('error', function (error) {
                setPending(false)
            })
            .then(function (receipt) {
                setPending(false);
            });
    }

    const burnLIT = () => {
        console.log('burn')
        const web3 = new Web3(window.provider);
        const lit_contract = new web3.eth.Contract(lit_abi, LIT_CONTRACT_ADDRESS);

        lit_contract.methods.performBurn(web3.utils.toWei(amount))
            .send({from: wallet.address})
            .once('transactionHash', function (tx) {
                setPending(true)
            })
            .on('error', function (error) {
                setPending(false)
            })
            .then(function (receipt) {
                setPending(false);
            });
    }

    const distChadTax = () => {
        const web3 = new Web3(window.provider);
        const chad_contract = new web3.eth.Contract(chad_abi, wallet.chain === CHAIN_IDS.BNB ? CHAD_CONTRACT_ADDRESS : CHADARB_CONTRACT_ADDRESS);

        chad_contract.methods.distributeTaxes()
            .send({from: wallet.address})
            .once('transactionHash', function (tx) {
                setPending(true)
            })
            .on('error', function (error) {
                setPending(false)
            })
            .then(function (receipt) {
                setPending(false);
                getInfo();
            });
    }

    const processChadBurnTax = () => {
        const web3 = new Web3(window.provider);

        const processABI = [
            {
                "inputs": [],
                "name": "process",
                "outputs": [],
                "stateMutability": "nonpayable",
                "type": "function"
            }
        ]

        const CHAD_BURNER_CONTRACT = new web3.eth.Contract(processABI, wallet.chain === CHAIN_IDS.BNB ? CHAD_BURNER : CHADARB_BURNER);

        CHAD_BURNER_CONTRACT.methods.process()
            .send({from: wallet.address})
            .once('transactionHash', function (tx) {
                setPending(true)
            })
            .on('error', function (error) {
                setPending(false)
            })
            .then(function (receipt) {
                setPending(false);
                getInfo();
            });
    }

    const processChadABTax = () => {
        const web3 = new Web3(window.provider);

        const processABI = [
            {
                "inputs": [],
                "name": "process",
                "outputs": [],
                "stateMutability": "nonpayable",
                "type": "function"
            }
        ]

        const CHAD_ASSET_BACKING_CONTRACT = new web3.eth.Contract(processABI, wallet.chain === CHAIN_IDS.BNB ? CHAD_ASSET_BACKING : CHADARB_ASSET_BACKING);

        CHAD_ASSET_BACKING_CONTRACT.methods.process()
            .send({from: wallet.address})
            .once('transactionHash', function (tx) {
                setPending(true)
            })
            .on('error', function (error) {
                setPending(false)
            })
            .then(function (receipt) {
                setPending(false);
                getInfo();
            });
    }

    const processChadStakingTax = () => {
        const web3 = new Web3(window.provider);

        const processABI = [
            {
                "inputs": [],
                "name": "process",
                "outputs": [],
                "stateMutability": "nonpayable",
                "type": "function"
            }
        ]

        const CHAD_STAKING_CONTRACT = new web3.eth.Contract(processABI, wallet.chain === CHAIN_IDS.BNB ? CHAD_STAKING : CHADARB_STAKING);

        CHAD_STAKING_CONTRACT.methods.process()
            .send({from: wallet.address})
            .once('transactionHash', function (tx) {
                setPending(true)
            })
            .on('error', function (error) {
                setPending(false)
            })
            .then(function (receipt) {
                setPending(false);
                getInfo();
            });
    }

    const distLitBNBTax = () => {
        const web3 = new Web3(window.provider);
        const chad_contract = new web3.eth.Contract(litsrg20_abi, LITBNB_CONTRACT_ADDRESS);

        chad_contract.methods.withdrawTaxBalance()
            .send({from: wallet.address})
            .once('transactionHash', function (tx) {
                setPending(true)
            })
            .on('error', function (error) {
                setPending(false)
            })
            .then(function (receipt) {
                setPending(false);
                getInfo();
            });
    }

    return <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
        {
            wallet.address === COINEUS_FUND_ADDRESS && <>
                <Grid item xs={12}>
                    <Paper style={{padding: 20}}>
                        <Typography variant="h6">Burn CEUS</Typography>
                        <FormControl fullWidth style={{marginBottom: 20, marginTop: 20}}>
                            <TextField
                                autoComplete="off"
                                type="number"
                                value={amount}
                                placeholder="Enter Amount"
                                onChange={(ev) => {
                                    setAmount(ev.target.value)
                                }}
                            />
                        </FormControl>
                        <NetworkButton network={CHAIN_IDS.FUSE}>
                            <Button fullWidth variant="contained"
                                    disabled={!amount}
                                    onClick={burn}>BURN CEUS</Button>
                        </NetworkButton>
                    </Paper>
                </Grid>
                <Grid item xs={12}>
                    <Paper style={{padding: 20}}>
                        <Typography variant="h6">Burn LIT</Typography>
                        <FormControl fullWidth style={{marginBottom: 20, marginTop: 20}}>
                            <TextField
                                autoComplete="off"
                                type="number"
                                value={amount}
                                placeholder="Enter Amount"
                                onChange={(ev) => {
                                    setAmount(ev.target.value)
                                }}
                            />
                        </FormControl>

                        <NetworkButton network={CHAIN_IDS.FUSE}>
                            <Button fullWidth variant="contained"
                                    disabled={!amount}
                                    onClick={burnLIT}>BURN LIT</Button>
                        </NetworkButton>
                    </Paper>
                </Grid>
                <Grid item xs={12}>
                    <Paper style={{padding: 20}}>
                        <Typography variant="h6">CHAD BNB TAX BALANCE</Typography>
                        <List dense>
                            <ListItem>
                                <ListItemText
                                    primary={CoineusCryptoFormat(formatWeiToNumber(chadTaxBal, 9)) + " SRG"}
                                    secondary="Pending Tax Balance"
                                />
                            </ListItem>
                            <ListItem>
                                <ListItemText
                                    primary={CoineusCryptoFormat(formatWeiToNumber(pendingCHADBurn, 9)) + " SRG"}
                                    secondary="Pending CHAD BNB Burn"
                                />
                            </ListItem>
                        </List>


                        <Grid container spacing={2}>
                            <Grid item>
                                <NetworkButton network={CHAIN_IDS.BNB}>
                                    <Button variant="contained" onClick={distChadTax}>Disburse Taxes</Button>
                                </NetworkButton>
                            </Grid>
                            <Grid item>
                                <NetworkButton network={CHAIN_IDS.BNB}>
                                    <Button variant="contained" onClick={processChadBurnTax}>Process Burn</Button>
                                </NetworkButton>
                            </Grid>
                            <Grid item>
                                <NetworkButton network={CHAIN_IDS.BNB}>
                                    <Button variant="contained" onClick={processChadABTax}>Process Asset Backing</Button>
                                </NetworkButton>
                            </Grid>
                            <Grid item>
                                <NetworkButton network={CHAIN_IDS.BNB}>
                                    <Button variant="contained" onClick={processChadStakingTax}>Process Staking</Button>
                                </NetworkButton>

                            </Grid>
                        </Grid>
                    </Paper>
                </Grid>
                <Grid item xs={12}>
                    <Paper style={{padding: 20}}>
                        <Typography variant="h6">CHAD ARB TAX BALANCE</Typography>
                        <List dense>
                            <ListItem>
                                <ListItemText
                                    primary={CoineusCryptoFormat(formatWeiToNumber(chadARBTaxBal, 9)) + " SRG"}
                                    secondary="Pending Tax Balance"
                                />
                            </ListItem>
                            <ListItem>
                                <ListItemText
                                    primary={CoineusCryptoFormat(formatWeiToNumber(pendingCHADARBBurn, 9)) + " SRG"}
                                    secondary="Pending CHAD ARB Burn"
                                />
                            </ListItem>
                        </List>


                        <Grid container spacing={2}>
                            <Grid item>
                                <NetworkButton network={CHAIN_IDS.ARB}>
                                    <Button variant="contained" onClick={distChadTax}>Disburse Taxes</Button>
                                </NetworkButton>
                            </Grid>
                            <Grid item>
                                <NetworkButton network={CHAIN_IDS.ARB}>
                                    <Button variant="contained" onClick={processChadBurnTax}>Process Burn</Button>
                                </NetworkButton>
                            </Grid>
                            <Grid item>
                                <NetworkButton network={CHAIN_IDS.ARB}>
                                    <Button variant="contained" onClick={processChadABTax}>Process Asset Backing</Button>
                                </NetworkButton>
                            </Grid>
                            <Grid item>
                                <NetworkButton network={CHAIN_IDS.ARB}>
                                    <Button variant="contained" onClick={processChadStakingTax}>Process Staking</Button>
                                </NetworkButton>

                            </Grid>
                        </Grid>
                    </Paper>
                </Grid>
                <Grid item xs={12}>
                    <Paper style={{padding: 20}}>
                        <Typography variant="h6">LIT BNB TAX BALANCE</Typography>
                        <List dense>
                            <ListItem>
                                <ListItemText
                                    primary={CoineusCryptoFormat(formatWeiToNumber(litBNBTaxBal, 9)) + " SRG"}
                                    secondary="Pending Tax Balance"
                                />
                            </ListItem>
                        </List>

                        <NetworkButton network={CHAIN_IDS.BNB}>
                            <Button fullWidth variant="contained" onClick={distLitBNBTax}>Disburse Taxes</Button>
                        </NetworkButton>
                    </Paper>
                </Grid>
            </>
        }
        <CoineusLoading open={pending} label="Transaction Pending"/>
    </Grid>
}